.fog-anim{
    position: absolute;
    width:100%;
    padding-top: 100%;
    height: 100%;
    z-index:1;
    left:0;
    top:200px;
    background:url(//paranormal39.github.io/static/media/fog.97672296.png) repeat-x;
    background-size: 50% auto;
    background-position: 0 top;
    -webkit-animation: fogAnim 60s;
            animation: fogAnim 60s;
    -webkit-animation-timing-function: linear;
            animation-timing-function: linear;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
   
    }
   
    
    @-webkit-keyframes fogAnim{
    0%{background-position: -200% bottom}
    100%{background-position: 0% bottom}
    
    }
   
    
    @keyframes fogAnim{
    0%{background-position: -200% bottom}
    100%{background-position: 0% bottom}
    
    }
